import skeleton from "../../components/quick-skeleton/quick-skeleton.vue";
import MescrollUni from "@/components/mescroll-uni/mescroll-uni.vue";

export default {
    data () {
        return{
            mescroll: null, //mescroll实例对象
            upOption:{
                page: {
                // 	num: 0, // 当前页码,默认0,回调之前会加1,即callback(page)会从1开始
                    size: 5, // 每页数据的数量
                },
                noMoreSize: 4, //如果列表已无数据,可设置列表的总数量要大于半页才显示无更多数据;避免列表数据过少(比如只有一条数据),显示无更多数据会不好看; 默认5
                empty:{
                tip: '~ 这里没有发现任何数据哦 ~', // 提示
                btnText: '返回首页'
                }
            },
            tipShow: false,
            showSkeleton: true,  //骨架屏显示隐藏
            isChoose: false,
            list: [
                {
                    commission_rate: "20.01",
                    coupon_end_time: "2019-06-29",
                    coupon_info: "5",
                    coupon_remain_count: "76000",
                    coupon_short_url: "https://s.click.taobao.com/FDgUl6w",
                    coupon_total_count: "100000",
                    ittle_images: [{
                        imgUrl: "https://img.alicdn.com/i2/2452461494/O1CN01SMpb0M1MuIGBrXeKR_!!2452461494.jpg",
                        isChoose: false
                    },
                    {
                        imgUrl: "https://img.alicdn.com/i2/2452461494/O1CN01SMpb0M1MuIGBrXeKR_!!2452461494.jpg",
                        isChoose: false
                    },
                    {
                        imgUrl: "https://img.alicdn.com/i2/2452461494/O1CN01SMpb0M1MuIGBrXeKR_!!2452461494.jpg",
                        isChoose: false
                    },
                    {
                        imgUrl: "https://img.alicdn.com/i2/2452461494/O1CN01SMpb0M1MuIGBrXeKR_!!2452461494.jpg",
                        isChoose: false
                    }],
                    origin_id: "596563457177",
                    tbk_pwd: "￥8ZNFYgb1uny￥",
                    thumb: "http://apiqr.vephp.com/public/uploads/modules/tmp/tklpic/VEUGOARBAwVTA1cOAmYGYldlAT4FG1NW.jpg",
                    title: "第2件1第3件0.1【誉福园】新鲜水果水蜜桃毛桃子拍3发10斤带箱"
                }
            ]
        }
    },
    components: {
        skeleton,
        MescrollUni
    },
    onReady () {
        const that = this;
        that.$refs.skeleton.attachedAction();
        that.$refs.skeleton.readyAction();
    },
    methods: {
        //  mescroll组件初始化的回调,可获取到mescroll对象
        mescrollInit(mescroll) {
        this.mescroll = mescroll;
        },
        /*下拉刷新的回调 */
        downCallback(mescroll) {
        // 这里加载你想下拉刷新的数据, 比如刷新轮播数据
        // loadSwiper();
        // 下拉刷新的回调,默认重置上拉加载列表为第一页 (自动执行 mescroll.num=1, 再触发upCallback方法 )
        mescroll.resetUpScroll()
        },
        /*上拉加载的回调: mescroll携带page的参数, 其中num:当前页 从1开始, size:每页数据条数,默认10 */
        upCallback(mescroll) {
        //联网加载数据
        this.getListDataFromNet(mescroll.num, mescroll.size, (curPageData)=>{
            //联网成功的回调,隐藏下拉刷新和上拉加载的状态;
            mescroll.endSuccess(curPageData.length);
            //设置列表数据
            if(mescroll.num == 1) this.list = []; //如果是第一页需手动制空列表
            this.list=this.list.concat(curPageData); //追加新数据
        }, () => {
            //联网失败的回调,隐藏下拉刷新的状态
            mescroll.endErr();
        })
        },
        //点击空布局按钮的回调
        emptyClick(){
        uni.switchTab({
            url: '/pages/index/index'
        });
        },
        async getListDataFromNet(pageNum,pageSize,successCallback,errorCallback) {
            try {
                const list = await this.$request.post("/user/one-share", {
                    data: {
                        page: pageNum,
                        page_size: pageSize,
                        version: '1.2.5'
                    }
            });
            list.data.data.map((value,key) => {
                const textValue = `${value.title}
淘口令：${value.tbk_pwd}
短连接：${value.coupon_short_url}`
                value.textValue = textValue;
                value.isChoose = true;
                value.ittle_images = []

                for (const item of value.small_images) {
                    const json = {
                        imgUrl: item,
                        isChoose: false
                    }
                    value.ittle_images.push(json)
                }

            })
            this.showSkeleton = false;

            successCallback && successCallback(list.data.data);
            } catch (error) {
                console.error("error:", error);
            }
        },
        choose (index) {
            this.list[index].isChoose = this.list[index].isChoose?false:true;
        },
        choose2 (idx,index) {
            this.list[idx].ittle_images[index].isChoose = this.list[idx].ittle_images[index].isChoose?false:true;
        },
        // 一键分享
        share(item){
            uni.setClipboardData({
                data: item.title + item.tbk_pwd,
                success: function () {
                    uni.showToast({
                        title: '复制淘口令成功',
                        icon: 'none'
                    });
                        // console.log('复制成功');
                }
            });
            if(item.isChoose){
                uni.saveImageToPhotosAlbum({
                    filePath: item.thumb,
                    success: function () {
                        uni.showToast({
                            title: '已成功保存主图至您的相册，快去分享吧',
                            icon: 'none'
                        });
                    }
                });
            }
            item.ittle_images.map((value,key) => {
                if(value.isChoose) {
                    uni.saveImageToPhotosAlbum({
                        filePath: value.imgUrl,
                        success: function () {
                            uni.showToast({
                                title: '已成功保存图片至您的相册，快去分享吧',
                                icon: 'none'
                            });
                        }
                    });
                }
            })
        }
    }
}
